@nav-width: 88%;
@nav-indexing: 5;
@collapsed-nav-width: 100%;
.nih {
    width: @nav-width;
    z-index: @nav-indexing;
    height: 97px;
    background-color: #00509b;
    background-image: url(../images/nih-off.png);
    background-repeat: no-repeat;
    background-size: 67px 42px;
    background-position: 10% center;
    padding: 29px 0px 0px 89px;
    color: #565e66;
    font-size: 17px;
    @media (max-width: @screen-md) {
        font-size: 1.2em;
        line-height: 1.1em;
        background-image: none;
        width: @collapsed-nav-width;
        height: 20%;
        display: block;
        margin: 0 !important;
        padding: 8px 0px 6px 74px !important;
    }
    @media (max-width: 767px) {
        display: block;
        margin: 0 !important;
        padding: 8px 0px 6px 74px !important;
        width: 100%;
        background-position: 5% center;
        background-size: 30px 22px;
        height: 20%;
    }
    &:first-child {
        background-color: #f3f3f3;
        padding: 29px 0px 0px 89px;
        &:hover,
        &:focus {
            background-color: #00509b;
            background-image: url(../images/nih-on.png);
            color: #fff;
            @media (max-width: @screen-md) {
                font-size: 1.2em;
                line-height: 1.1em;
                background-image: none;
            }
        }
    }
}
