// Links in navbars
//
// Add a class to ensure links outside the navbar nav are colored correctly.
// former long classes for slider tabs //
.slider-tabs1 {
    background-color: #1f4675;
    position: relative;
    display: inline-block;
    @media (max-width: 767px) {
        display: none;
        top: 241px;
        width: 100%;
        position: absolute;
        height: 20%;
        font-size: 24px;
        margin: 0;
        padding: 3px 0 1px 15px;
        transition: .3s;
        line-height: 1.8em;
        z-index: 99;
    }
}
.slider-tabs1.flex-active::after {
    border-bottom: 20px solid #1f4675;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    width: 0;
    height: 0;
    margin-left: -20px;
    content: "";
    display: block;
    position: absolute;
    bottom: 100%;
    left: 50%;
    z-index: 2;
    @media (max-width: @screen-sm) {
        display: none;
    }
}
